@import "doc";

html, body{
    margin:0;
    padding:0;
}
body{
    background-image: url(data:image/png;base64,);
    background-color: #efefef;
}

.page{
    margin: 20px auto;
    box-shadow: 0px 0px 15px 2px;
}
.controls{
    padding-top:2em;
    background-color:#fff;
    width: 30%;
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    overflow-y: auto;
    overflow-x: hidden;

    input[type="text"], select{
        @extend .form-control;
    }
    input[type="submit"]{
        @extend .btn;
        @extend .btn-default;
    }
    form {
        p {
            text-align:center;
            .btn{
                margin:4px;
            }
        }
        & > fieldset {
            text-align: center;
            & > fieldset {
                text-align: center;
                legend {
                    border:none;
                    font-size: 1.2em;
                }
                & > fieldset {
                    legend {
                        text-align: center;
                        font-size:1em;
                        margin-left:40px;
                        margin-bottom:6px;
                        font-style:italic;
                    }
                }
            }
        }
    }
}

.preview{
    width:70%;
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    overflow: auto;
}

@media screen {
    .page {

  }
}



@media print {

  @page {
    size: 210mm 297mm;
    margin: 20mm;
    padding:0;
  }

    .controls{
        display:none;
    }
    .preview{
        width:100%;
        position: static;
        overflow:hidden;
        .page {
            margin: 0;
            border: initial;
            border-radius: initial;
            width: initial;
            min-height: initial;
            box-shadow: initial;
            background: initial;
                overflow: hidden;
        }
    }
}


